import React from "react";
import "./styles.css";

import { Formik, Field, Form } from "formik";

class InputField extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      score: null,
      color: "#888",
      characteristics: {},
    };
  }

  checkPassword(pass) {
    this.checkPass(pass);
    let strength;
    let sameCaseRegex = /(^[a-z]*$)|(^[A-Z]*$)/g.test(pass);
    let symbolRegex = /.*[?!"£$%^&*|/\\\][(){}<>,.:;#~`¬=_+\-@'].*/g.test(pass);

    //POINTS BASED SYSTEM INSTEAD??

    if (pass.length < 8) strength = "very weak";
    else if (sameCaseRegex) strength = "weak";
    else if (!symbolRegex) strength = "strong";
    else strength = "very strong";

    this.setState({ strength });
  }

  checkPass(pass) {
    if (pass === "") return;

    let color = "#888",
      score = 5;
    let commonPass = ["12345678", "password", "Password!", "p4ssw0rd", "pass"];
    let characteristics = {
      tooShort: false,
      isCommon: false,
      contains: {
        symbol: true,
        number: true,
        mixedCase: true,
      },
    };

    let hasSameCase =
        pass === pass.toUpperCase() || pass === pass.toLowerCase(),
      hasNum = /.*[0-9].*/g.test(pass),
      hasSymbol = /.*[?!"£$%^&*|/\\\][(){}<>,.:;#~`¬=_+\-@'].*/g.test(pass),
      isCommon = commonPass.includes(pass),
      tooShort = pass.length < 8;

    if (isCommon) {
      score--;
      characteristics.isCommon = true;
    }
    if (tooShort) {
      score--;
      characteristics.tooShort = true;
    }
    if (hasSameCase) {
      score--;
      characteristics.contains.mixedCase = false;
    }
    if (!hasSymbol) {
      score--;
      characteristics.contains.symbol = false;
    }
    if (!hasNum) {
      score--;
      characteristics.contains.number = false;
    }

    color = this.getColor(score);

    this.setState({ color, characteristics, score });
  }

  getColor(score) {
    switch (score) {
      case 0:
        return "#A00";
      case 1:
        return "#B33";
      case 2:
        return "#F55";
      case 3:
        return "#BB5";
      case 4:
        return "#8B3";
      case 5:
        return "#3A3";
      default:
        return "#888";
    }
  }

  render() {
    return (
      <div
        className="passContainer"
        style={{ backgroundColor: this.state.color }}
      >
        <Formik
          initialValues={{ password: "" }}
          onSubmit={(obj) => this.checkPass(obj.password)}
        >
          <Form className="form">
            <label>Enter your password: </label>
            <Field id="password" name="password" placeholder="Password" />
            <br />
            <button type="submit">Check</button>
          </Form>
        </Formik>

        <h1>
          {this.state.score != null
            ? "Score: " + this.state.score
            : "Try out a password, and we'll test its strength!"}
        </h1>
        <h1>{this.state.score === 5 ? "Perfect score! Well done!" : ""}</h1>
      </div>
    );
  }
}

export default InputField;
